* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  color: white;
  background-color: #2e2e2e;
}
.App {
  font-family: sans-serif;
  text-align: center;
}
.App h1 {
  margin: 1em;
}

img {
  width: 100%;
  height: 100%;
}
.card {
  width: 100%;
  height: 70%;
  border: 10px solid white;
  filter: opacity(0.7);
}
.card:hover {
  filter: opacity(1);
}
.App .Memes {
  padding: 1em;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1em;
}
/* style={{width:"400px",margin:"auto"}} */
.memeCard {
  width: 400px;
  margin: auto;
}

.memeCard input {
  width: 100%;
  padding: 10px;
  outline: none;
}

.memeCard .memeActionPanel {
  display: flex;
  flex-direction: column;
  margin: 1em;
}
.memeActionPanel button {
  padding: 1em;
  color: white;
  background: black;
  outline: none;
  border: inset 0 0 5px white;
}
